export default ".container {\n    max-width: 1200px;\n    margin: 0 auto;\n    padding: 20px;\n}\n\nheader {\n    margin-bottom: 20px;\n    background-color: black;\n    padding: 10px;\n}\n\nheader h1 {\n    font-size: 2rem;\n    color: #fff;\n}\n\nmain {\n    text-align: center;\n}\n\nmain h2 {\n    color: #ffffff;\n    font-size: 2rem;\n    margin-top: 40px;\n    margin-bottom: 60px;\n}\n\n.typing-text {\n    display: inline-block;\n    overflow: hidden;\n    border-right: .15em solid #fff;\n    white-space: nowrap;\n    letter-spacing: .15em;\n    font-family: Poppins;\n    animation: typing 4s steps(40, end), blink-caret .75s step-end infinite;\n}\n\n.auth-btns-container {\n    height: 35px;\n    margin: 10px;\n    margin-right: 100px !important;\n}\n\n@keyframes typing {\n    from { width: 0 }\n    to { width: 70% }\n}\n\n@keyframes blink-caret {\n    from, to { border-color: transparent }\n    50% { border-color: #fff }\n}\n\n.card {\n    background-color: transparent;\n    transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;\n}\n\n.card:hover {\n    transform: scale(1.05);\n}\n\n.card:hover ~ .card,\n.card:hover ~ .card ~ .card {\n    opacity: 0.5;\n}\n\n.card-container {\n    text-align: start;\n    display: flex;\n    justify-content: space-around;\n    flex-wrap: wrap;\n}\n\nfooter {\n    background: #fff;\n    color: #000;\n    padding: 20px;\n    text-align: center;\n    margin-top: 40px;\n    border-top: 1px solid #ccc;\n}\n\n.payment-icons {\n    display: flex;\n    justify-content: center;\n    margin: 10px 0;\n}\n\n.payment-icons img {\n    margin: 0 10px;\n    width: 50px;\n}\n\n/* Media Queries */\n\n@media (max-width: 768px) {\n    .card-container {\n        flex-direction: column;\n        align-items: center;\n    }\n\n    .card {\n        width: 80%;\n        margin: 10px 0;\n    }\n}\n\n@media (max-width: 480px) {\n    header h1 {\n        font-size: 1.5rem;\n    }\n\n    main h2 {\n        font-size: 1.2rem;\n    }\n\n    .card {\n        width: 90%;\n        padding: 15px;\n    }\n\n    .card h3 {\n        font-size: 1rem;\n    }\n\n    .card .price {\n        font-size: 1.2rem;\n    }\n\n    .card button {\n        font-size: 0.9rem;\n        padding: 8px;\n    }\n\n    .payment-icons img {\n        width: 40px;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */";